.home {
    display: flex;
    flex-direction: column;
  }
.card_container {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 56px;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  background: #f7f7f7;
  border-radius: 25px;
  padding: 50px;
}
.card_container a {
  flex-grow: 1;
}
.card {
  flex-grow: 1;
  min-width: 340px;
  min-height: 340px;
  background: linear-gradient(
    180deg,
    rgba(218, 43, 43, 0) 0%,
    rgba(0, 0, 0, 0.5) 100%
  );
  border-radius: 10px;
  position: relative;
  background: linear-gradient(white, black);
  border-radius: 15px 15px 15px 15px;
}

.img_cover {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: 15px 15px 15px 15px;
  opacity: 0.8;
}

.card p {
  position: absolute;
  bottom: 0;
  color: white;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 142.6%;
  padding-left: 10px;
}

@media (max-width : 1024px) {
.card_container {
  background-color: white;
}


}