.Header {
    display: flex;
    justify-content: space-between;
    margin-left: 100px;
    margin-right: 100px;
    margin-top: 20px;
  }
  
  .header-logo {
    margin: 10px;
  }
  .logo {
    width: 270px;
  }
  .header-lists ul {
    display: flex;
    list-style: none;
    gap: 55px;
  }
  .header-lists ul li {
    padding: 10px;
    color: #ff6060;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 35px;
  }
  .navlinkk {
    text-decoration: none;
    color: #ff6060;
  }

  @media (max-width: 1024px) {
    .logo {
      width: 170px;
    }
   .header-lists ul li {
    font-size: 20px;
    margin-block-start: 0px;
    margin-block-end: 0px;
  }
  .header-lists ul  {
 
    padding-inline-start: 0px;
    gap : 10px; 
  }
