.slider {
    margin-top: 50px;
    margin-bottom: 50px;
    width: calc(100% - 200px);
    height: 450px;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;  
    justify-content: space-between;
  }
  .nombresdimages {
    width: 150px;
    height: 150px;
    z-index: 99;
    color: white;
    font-weight: 600;
    font-size: 18px;
    align-self: END;
    display: flex;
    justify-content: center;
    align-items: center;

  
  }
  .imagesinslider {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 4;
    border-radius: 25px;
    object-fit: cover;
  }
  .right {
     z-index: 99;
     width: 48px;
     height: 80px;
    cursor: pointer;
    padding-right: 10px;
     
   
  }
  .left {
     z-index: 99;
     width: 48px;
     height: 80px;
    cursor: pointer;
    cursor: pointer;
    padding-left: 10px;

  
  }

  @media (max-width: 1024px) {
.slider {

height: 347px;
width: calc(100% - 100px);

}
    .left , .right {
      width: 37px;
      height: 58px;
  
    }
  }

  @media (max-width: 650px) {
    .slider {

      height: 247px;
      
      }
          .left , .right {
            width: 37px;
            height: 58px;
        
          }
        }
  